import React from 'react';
import {INF} from './Vertex'
export function dfs(v,visited,mat,n) {
    let stack = []
    stack.push(v)
    visited[v] = 1
    while(stack.length != 0) {
        let top = stack[stack.length-1]
        let flag = 0
        for(let i = 0; i < n; i++) {
            if((mat[top][i]!=INF && visited[i]) || mat[top][i]==INF) {
                flag++
            }
        }
        if(flag == n) {
            stack.pop()
            if(stack.length!=0) top = stack[stack.length-1]
        }
        for(let i = 0; i < n; i++) {
            if(!visited[i] && mat[top][i] != INF) {
                stack.push(i)
                visited[i] = 1
                break
            }
        }
    }
    for(let i = 0; i < n; i++) {
        if(!visited[i]) return 0
    }
    return 1
}
export function getDistance(line) {
    let distance = Math.sqrt(Math.pow((line.x1-line.x2),2)+Math.pow((line.y1-line.y2),2))
    return Math.floor(distance/10)
}
