import React from 'react';
import {Layout} from "../components/Layout";
import {Button, ButtonGroup, Col, Dropdown, DropdownButton, Row} from "react-bootstrap";
import {canvasHeight, canvasWidth} from "./bfs";

const HomePage = () => {

    return (
        <Layout>
            <Row className="no-gutter">
                <Col md={7} className="bg-secondary bg-opacity-10">
                    <ButtonGroup aria-label="Basic example" className="p-2">
                        <Button variant="secondary" >CLEAR</Button>
                        <Button variant="secondary" >VERTEX</Button>
                        <DropdownButton as={ButtonGroup} title="EDGE" variant="secondary">
                            <input placeholder="vertex1" />
                            <input placeholder="vertex2" />
                            <input placeholder="weight" />
                            <Dropdown.Item >GENERATE</Dropdown.Item>
                        </DropdownButton>
                        <DropdownButton as={ButtonGroup} title="GRAPH" variant="secondary">
                            <input placeholder="count" />
                            <Dropdown.Item >GENERATE</Dropdown.Item>
                        </DropdownButton>
                        <Button variant="secondary" >REAL DISTANCE</Button>
                        <DropdownButton as={ButtonGroup} title="ANIMATION" variant="secondary">
                            <input placeholder="vertex1" />
                            <Dropdown.Item >START</Dropdown.Item>
                        </DropdownButton>
                    </ButtonGroup>
                    <div>
                        <div style={{height: 300}}>
                            <p>
                                This area
                            </p>
                        </div>
                        <div className="text-center">
                            <input type="range" min={0} defaultValue={0} className="p-3 slider" id="slider"/>
                        </div>
                    </div>
                    <div className="bg-secondary bg-opacity-10">
                        <div id="instruction"></div>
                        <div id="execution" ></div>
                    </div>
                </Col>
                <Col md={5} className="bg-secondary bg-opacity-25">
                    <div className="bg-secondary bg-opacity-25">
                        <h3 className="text-secondary p-3">Code Area</h3>
                    </div>
                    <div id="code">
                        <pre className="p-3">

</pre>
                    </div>
                </Col>
            </Row>
        </Layout>
    )
}

export default HomePage;