import React, {useEffect, useRef, useState} from "react";
import {Vertex} from "../components/Vertex";
import {Edge} from "../components/Edge";
import {Frame} from "../components/Frame";
import {Graph} from "../components/Graph";
import {COLOR} from "../components/Vertex";
import {INF} from "../components/Vertex";
import {Layout} from "../components/Layout";
import {ButtonGroup, Button, Dropdown, DropdownButton, Container,Row,Col} from "react-bootstrap";
import {dfs,getDistance} from "../components/utils";

export const canvasWidth = 600;
export const canvasHeight = 300;

const DFS = () => {
    const [instruction, setInstruction] = useState(" ");
    const [execution, setExecution] = useState(" ");
    const [vertexes, setVertexes] = useState([]);
    const [edges, setEdges] = useState([]);
    const [animation,setAnimation] = useState([]);
    let sourcePoint = 0;
    let count = 0;
    let isDown = false;
    let dragTarget = null;
    let dragEdge = [];
    let startX = null;
    let startY = null;
    let ctx = null;
    /*let vertexes = [];
    let edges = [];*/
    let randX = canvasWidth - 200
    let randY = canvasHeight - 200
    const canvas = useRef(null);
    let edge = {vertexe1:null, vertexe2:null, weight:null}

    useEffect(()=>{
        const canvasEle = canvas.current;
        canvasEle.width = canvasWidth;
        canvasEle.height = canvasHeight;

        // get context of the canvas
        ctx = canvasEle.getContext("2d");
        ctx.clearRect( 0,0, canvasWidth, canvasHeight );
        edges.forEach((value, index, array)=>{array[index].drawEdge(ctx)});
        vertexes.forEach((value, index, array)=>{array[index].drawVertex(ctx)});

    });

    /*    useEffect(() => {
            draw();
        }, []);*/

    const draw = () => {
        ctx.clearRect( 0,0, canvasWidth, canvasHeight );
        edges.forEach((value, index, array)=>{array[index].drawEdge(ctx)});
        vertexes.forEach((value, index, array)=>{array[index].drawVertex(ctx)});

    }
    let drawFrame = function (i,animation) {
        animation[i].edgesColor.forEach((value, index, array)=>{
            edges[index].color = value
        })
        animation[i].vertexesColor.forEach((value, index, array)=>{
            vertexes[index].color = value
        })
        animation[i].distance.forEach((value, index, array)=>{
            vertexes[index].d = value
        })
        setInstruction(animation[i].instruction);
        setExecution(animation[i].execution);
        setVertexes(vertexes);
        setEdges(edges);
        ctx.clearRect( 0,0, canvasWidth, canvasHeight );
        edges.forEach((value, index, array)=>{array[index].drawEdge(ctx)});
        vertexes.forEach((value, index, array)=>{array[index].drawVertex(ctx)});

    }

    function animate(animation) {
        let index = 0;
        let timer = setInterval(frame, 500)
        function frame() {
            if (index == animation.length) {
                clearInterval(timer)
            } else {
                drawFrame(index,animation)
                //draw()
                index++
            }
        }
    }

    const containsPoint = (x, y) => {
        let isTarget = null;
        for (let i = 0; i < vertexes.length; i++) {
            const circle = vertexes[i];
            let distance = Math.sqrt(Math.pow((circle.x-x),2)+Math.pow((circle.y-y),2))
            if(distance < circle.radius*3/2) {
                dragTarget = circle;
                for(let j = 0; j < edges.length; j++) {
                    if(edges[j].v1 == dragTarget.num)
                        dragEdge.push({num: j, v: 1});
                    if(edges[j].v2 == dragTarget.num)
                        dragEdge.push({num: j, v: 2});
                }
                isTarget = true;
                break;
            }
        }
        return isTarget;
    }

    const handleCanvasClick=(event)=>{
        const X = Math.random()*(randX)+100;
        const Y = Math.random()*(randY)+100;
        const newVertex = new Vertex(X,Y,COLOR.PINK,vertexes.length)
        vertexes.push(newVertex);
        draw();
    };

    const handleMouseMove = (event)=>{
        if (!isDown) return;
        const mouseX = parseInt(event.nativeEvent.offsetX - canvas.current.clientLeft);
        const mouseY = parseInt(event.nativeEvent.offsetY - canvas.current.clientTop);
        const dx = mouseX - startX;
        const dy = mouseY - startY;
        startX = mouseX;
        startY = mouseY;
        dragTarget.x += dx;
        dragTarget.y += dy;
        if(dragEdge.length != 0){
            for(let i = 0; i < dragEdge.length; i++) {
                if (dragEdge[i].v == 1) {
                    edges[dragEdge[i].num].x1 += dx;
                    edges[dragEdge[i].num].y1 += dy;
                }
                if (dragEdge[i].v == 2) {
                    edges[dragEdge[i].num].x2 += dx;
                    edges[dragEdge[i].num].y2 += dy;
                }
            }
        }
        draw();
    };

    const handleMouseDown = (event)=>{
        startX = parseInt(event.nativeEvent.offsetX - canvas.current.clientLeft);
        startY = parseInt(event.nativeEvent.offsetY - canvas.current.clientTop);
        isDown = containsPoint(startX,startY)
    };

    const handleMouseUp = e => {
        dragTarget = null;
        dragEdge = [];
        isDown = false;
    }
    const handleMouseOut = e => {
        handleMouseUp(e);
    }

    const handleClearCanvas=(event)=>{
        setVertexes([]);
        setEdges([]);
        setExecution(" ");
        setInstruction(" ");
    };

    const inputChange1=(event)=>{
        edge.vertexe1 = event.target.value;
    };
    const inputChange2=(event)=>{
        edge.vertexe2 = event.target.value;
    };
    const inputChange3=(event)=>{
        edge.weight = event.target.value;
    };
    const inputChange4=(event)=>{
        count = event.target.value;
    };
    const inputSourcePoint=(event)=>{
        sourcePoint = event.target.value;
    };
    const sliderChange=(event)=>{
        if(animation.length > 0) {
            event.target.max = animation.length - 1;
            drawFrame(event.target.value,animation);
        }
    };
    const realDistance=(event)=>{
        edges.forEach((value, index, array)=>{
            array[index].weight = getDistance(array[index])
        })
        setEdges(edges);
        draw()
    }
    const handleGraphClick = (event)=>{
        for(let i = 0; i < count; i++) {
            vertexes.push(new Vertex(Math.random()*(randX)+100,Math.random()*(randY)+100,COLOR.PINK,vertexes.length))
        }
        let connected = 0
        while(!connected) {
            let flag = 1
            for(let j = 0; j < Math.floor(Math.random()*(vertexes.length*5)); j++) {
                let v1 = Math.floor(Math.random()*vertexes.length)
                let v2 = Math.floor(Math.random()*vertexes.length)
                let weight = Math.floor(Math.random()*100+1)
                for(let k = 0; k < edges.length; k++) {
                    if ((edges[k].v1 == v1 || edges[k].v1 == v2)
                        && (edges[k].v2 == v1 || edges[k].v2 == v2)) {
                        flag = 0
                        break
                    }
                }
                if(!(v1 == v2) && flag){
                    edges.push(new Edge(vertexes[v1], vertexes[v2], weight, COLOR.GREY))
                }
            }
            let graph = new Graph(vertexes.length)
            graph.insert(edges)
            connected = dfs(0,graph.visited,graph.mat,graph.n)
        }
        draw()
    }
    const handleEdgeClick = (event)=>{
        let v1 = edge.vertexe1;
        let v2 = edge.vertexe2;
        let weight = edge.weight;
        edges.push(new Edge(vertexes[v1], vertexes[v2], weight, COLOR.GREY))
        draw();
    };
    function kEdge() {
        this.begin = 0
        this.end = 0
        this.weight = 0
    }
    function find(parent, n) {
        while(parent[n] > 0) {
            n = parent[n]
        }
        return n
    }
    let getEdge = function (v1,v2,edges) {
        let num
        edges.forEach((value, index, array)=>{
            if((array[index].v1 == v1 || array[index].v1 == v2)
                && (array[index].v2 == v1 || array[index].v2 == v2)) {
                num = index
            }
        })
        return num
    }

    const dfs_ = ()=>{
        let frame
        let execution = "<br>"
        let instruction = " "
        let v = sourcePoint
        let graph = new Graph(vertexes.length)
        let stack = []
        let result = []
/*        if(v < 0 || v >= vertexCount || isNaN(v)) {
            alert("Vertex does not exist")
            return
        }
        if(graph.n == 0) {
            alert("Graph does not exist")
            return
        }
        disableButtons()*/
        animation.push(new Frame(vertexes.length,edges.length))
        let vertexesColor = new Array(vertexes.length)
        let edgesColor = new Array(edges.length)
        for(let i = 0; i < vertexes.length; i++) {
            vertexesColor[i] = COLOR.PINK
        }
        for(let i = 0; i < edges.length; i++) {
            edgesColor[i] = COLOR.GREY
        }
        graph.insert(edges)
        stack.push(v)
        result.push(v)
        graph.visited[v] = 1
        vertexesColor[v] = COLOR.GREEN
        frame = new Frame(vertexes.length,edges.length)
        frame.record(vertexesColor,edgesColor,instruction,execution)
        animation.push(frame)

        while(stack.length != 0) {
            let top = stack[stack.length-1]
            let flag = 0
            for(let i = 0; i < graph.n; i++) {
                if(!graph.visited[i] && graph.mat[top][i]!=INF) {
                    stack.push(i)
                    instruction = "stack | "+stack.join(" | ")
                    top = stack[stack.length-1]
                    graph.visited[i] = 1
                    result.push(i)
                    execution += i+" is not visited, "+"visit "+i+"<br><br>"
                    vertexesColor[i] = COLOR.GREEN
                    frame = new Frame(vertexes.length,edges.length)
                    frame.record(vertexesColor,edgesColor,instruction,execution)
                    animation.push(frame)
                    break
                }
            }
            let topColor = vertexesColor[top]
            vertexesColor[top] = COLOR.RED
            execution += "visit the adjacent vertexes of "+top+"<br>"
            for(let i = 0; i < graph.n; i++) {
                if((graph.mat[top][i]<INF && graph.visited[i])||graph.mat[top][i]==INF) {
                    flag++
                }
                if(graph.mat[top][i]<INF && graph.visited[i]) {
                    execution += i+" is visited<br>"
                    let color = vertexesColor[i]
                    vertexesColor[i] = COLOR.RED
                    edgesColor[getEdge(top,i,edges)] = COLOR.RED
                    frame = new Frame(vertexes.length,edges.length)
                    frame.record(vertexesColor,edgesColor,instruction,execution)
                    animation.push(frame)

                    vertexesColor[i] = color
                    edgesColor[getEdge(top,i,edges)] = COLOR.GREY
                    frame = new Frame(vertexes.length,edges.length)
                    frame.record(vertexesColor,edgesColor,instruction,execution)
                    animation.push(frame)
                }
            }
            vertexesColor[top] = topColor
            frame = new Frame(vertexes.length,edges.length)
            frame.record(vertexesColor,edgesColor,instruction,execution)
            animation.push(frame)
            if(flag == graph.n) {
                stack.pop()
                instruction = "stack | "+stack.join(" | ")
                execution += "All of the adjacent vertexes of "+top+" are visited<br><br>"
                if(stack.length!=0) top = stack[stack.length-1]
            }
        }
        instruction = "visit order: "+result.join(" ")
        frame = new Frame(vertexes.length,edges.length)
        frame.record(vertexesColor,edgesColor,instruction,execution)
        animation.push(frame)
        animate(animation);
    }

    return (
        <Layout>
            <Row className="no-gutter">
                <Col md={7} className="bg-secondary bg-opacity-10">
                    <ButtonGroup aria-label="Basic example" className="p-2">
                        <Button variant="secondary" onClick={handleClearCanvas}>CLEAR</Button>
                        <Button variant="secondary" onClick={handleCanvasClick}>VERTEX</Button>
                        <DropdownButton as={ButtonGroup} title="EDGE" variant="secondary">
                            <input placeholder="vertex1" onChange={inputChange1}/>
                            <input placeholder="vertex2" onChange={inputChange2}/>
                            <input placeholder="weight" onChange={inputChange3}/>
                            <Dropdown.Item onClick={handleEdgeClick}>GENERATE</Dropdown.Item>
                        </DropdownButton>
                        <DropdownButton as={ButtonGroup} title="GRAPH" variant="secondary">
                            <input placeholder="count" onChange={inputChange4}/>
                            <Dropdown.Item onClick={handleGraphClick}>GENERATE</Dropdown.Item>
                        </DropdownButton>
                        <Button variant="secondary" onClick={realDistance}>REAL DISTANCE</Button>
                        <DropdownButton as={ButtonGroup} title="ANIMATION" variant="secondary">
                            <input placeholder="vertex1" onChange={inputSourcePoint}/>
                            <Dropdown.Item onClick={dfs_}>START</Dropdown.Item>
                        </DropdownButton>
                    </ButtonGroup>
                    <div>
                        <canvas
                            className="App-canvas"
                            ref={canvas}
                            width={canvasWidth}
                            height={canvasHeight}
                            onMouseDown={handleMouseDown}
                            onMouseMove={handleMouseMove}
                            onMouseUp={handleMouseUp}
                            onMouseOut={handleMouseOut}/>
                        <div className="text-center">
                            <input type="range" min={0} defaultValue={0} className="p-3 slider" id="slider" onInput={sliderChange}/>
                        </div>
                    </div>
                    <div className="bg-secondary bg-opacity-10">
                        <div id="instruction" dangerouslySetInnerHTML={{__html:instruction}}></div>
                        <div id="execution" dangerouslySetInnerHTML={{__html:execution}}></div>
                    </div>
                </Col>
                <Col md={5} className="bg-secondary bg-opacity-25">
                    <div className="bg-secondary bg-opacity-25">
                        <h3 className="text-secondary p-3">Depth-first search</h3>
                    </div>
                    <div id="code">
                        <pre className="p-3">
    {`
function dfs(v,graph) {
    let stack = []
    stack.push(v)
    graph.visited[v] = 1
    while(stack.length != 0) {
        let top = stack[stack.length-1]
        let flag = 0
        for(let i = 0; i < graph.n; i++) {
            if((graph.mat[top][i] < Infinity && graph.visited[i]) || graph.mat[top][i] == Infinity) {
                flag++
            }
        }
        if(flag == graph.n) {
            stack.pop()
            if(stack.length!=0) top = stack[stack.length-1]
        }
        for(let i = 0; i < graph.n; i++) {
            if(!graph.visited[i] && graph.mat[top][i] != Infinity) {
                stack.push(i)
                graph.visited[i] = 1
                break;
            }
        }
    }
}
    `}
</pre>
                    </div>
                </Col>
            </Row>

        </Layout>
    )
}

export default DFS;