import React from 'react';

export function Edge(v1, v2, weight, color) {
    this.v1 = v1.num
    this.v2 = v2.num
    this.x1 = v1.x
    this.x2 = v2.x
    this.y1 = v1.y
    this.y2 = v2.y
    this.weight = weight
    this.color = color
}
Edge.prototype.drawEdge = function(context) {
    context.save()
    context.beginPath()
    context.moveTo(this.x1,this.y1)
    context.lineTo(this.x2,this.y2)
    context.closePath()
    context.strokeStyle = this.color
    context.stroke()
    let x = (this.x1+this.x2)/2
    let y = (this.y1+this.y2)/2
    context.fillStyle = "black"
    context.font = "20px serif"
    context.textAlign = "left"
    context.textBaseline = "alphabetic"
    context.fillText(this.weight,x,y)
    context.restore()
}