import React from 'react';
import {INF} from './Vertex';

export function Graph(n) {
    this.mat = new Array(n)
    this.visited = new Array(n)
    this.dist = new Array(n)
    this.n = n
    for(let i = 0; i < n; i++) {
        this.mat[i] = new Array(n)
        for(let j = 0; j < n; j++) {
            this.mat[i][j] = INF
        }
    }
    for(let i = 0; i < n; i++) {
        this.visited[i] = 0
        this.dist[i] = INF
    }
}

Graph.prototype.insert = function (edges) {
    edges.forEach((value,index,array)=>{
        let x = array[index].v1
        let y = array[index].v2
        let weight = array[index].weight
        this.mat[x][y] = weight
        this.mat[y][x] = weight
    })
}
