import React from 'react';
import {COLOR} from "./Vertex";

export function Frame(vertexCount, edgeCount) {
    this.vertexesCount = vertexCount
    this.edgeCount = edgeCount
    this.vertexesColor = new Array(vertexCount)
    this.edgesColor = new Array(edgeCount)
    this.distance = new Array(vertexCount)
    this.instruction = " "
    this.execution = "<br>"
    for(let i = 0; i < vertexCount; i++) {
        this.vertexesColor[i] = COLOR.PINK
    }
    for(let i = 0; i < edgeCount; i++) {
        this.edgesColor[i] = COLOR.GREY
    }
    for(let i = 0; i < vertexCount; i++) {
        this.distance[i] = -1
    }
}
Frame.prototype.record = function (vertexesColor,edgesColor,instruction,execution) {
    for(let i = 0; i < this.vertexesCount; i++) {
        this.vertexesColor[i] = vertexesColor[i]
    }
    for(let i = 0; i < this.edgeCount; i++) {
        this.edgesColor[i] = edgesColor[i]
    }
    this.instruction = instruction
    this.execution = execution
}
Frame.prototype.recordDistance = function (distance) {
    for(let i = 0; i < this.vertexesCount; i++) {
        this.distance[i] = distance[i]
    }
}
