import React from 'react';

export const INF = Number.MAX_SAFE_INTEGER


export const COLOR = {
    PINK: '#eaa0bd',
    GREY: '#383e3e',
    YELLOW: '#eef386',
    BLUE: '#5897ff',
    GREEN: '#6caa52',
    RED: '#e73688'
}


export function Vertex(x, y, color, num) {
    this.x = x
    this.y = y
    this.num = num
    this.color = color
    this.d = -1
    this.radius = 20
}

Vertex.prototype.drawVertex = function (context) {
    context.save()
    context.beginPath()
    context.arc(this.x, this.y, this.radius, 0, 360)
    context.closePath()

    context.fillStyle = this.color
    context.fill()

    context.fillStyle = "black"
    context.font = "25px serif"
    context.textAlign = "center"
    context.textBaseline = "middle"
    context.fillText(this.num, this.x, this.y)
    context.font = "20px serif"
    if(this.d != -1){
        if (this.d == INF)
            context.fillText("âˆž", this.x + 20, this.y + 20)
        else
            context.fillText(this.d, this.x + 20, this.y + 20)
    }
    context.restore()
}