import React from "react";
import {ListGroup, ListGroupItem} from "react-bootstrap";
import {Container,Row,Col,Navbar} from "react-bootstrap";
import {Link} from "react-router-dom";

export const Layout = ({ children }) => {
    return (

        <Container fluid className="no-gutter p-0">
                <Navbar expand="lg" variant="light" bg="light">
                    <Container>
                        <Navbar.Brand >Graph Algorithm Visualizer</Navbar.Brand>
                    </Container>
                </Navbar>
            <Row className="no-gutter">
                <Col md={2} className="bg-secondary bg-opacity-75">
                    <ListGroup variant="flush p-3" >
                        <ListGroup.Item action className="list-group-item-secondary">
                            <Link to="/" style={{color:'black',textDecoration:'none'}}> Home </Link>
                        </ListGroup.Item>
                        <ListGroup.Item action className="list-group-item-secondary">
                            <Link to="/bfs" style={{color:'black',textDecoration:'none'}}> Breadth-first search </Link>
                        </ListGroup.Item>
                        <ListGroup.Item action className="list-group-item-secondary">
                            <Link to="/dfs" style={{color:'black',textDecoration:'none'}}> Depth-first search </Link>
                        </ListGroup.Item>
                        <ListGroup.Item action className="list-group-item-secondary">
                            <Link to="/dijkstra" style={{color:'black',textDecoration:'none'}}> Dijkstra's algorithm </Link>
                        </ListGroup.Item>
                        <ListGroup.Item action className="list-group-item-secondary">
                            <Link to="/kruskal" style={{color:'black',textDecoration:'none'}}> Kruskal's algorithm </Link>
                        </ListGroup.Item>
                        <ListGroup.Item action className="list-group-item-secondary">
                            <Link to="/prim" style={{color:'black',textDecoration:'none'}}> Prim's algorithm </Link>
                        </ListGroup.Item>


                    </ListGroup>
                </Col>
                <Col md={10}>{children}</Col>

            </Row>
        </Container>
    );
};